import Vue from "vue";
import VueRouter from "vue-router";

import Home from "./views/Home.vue";
import AboutMe from "./components/NavMenu/AboutMe.vue";
import Community from "./components/NavMenu/Community.vue";
import Projects from "./components/NavMenu/Projects.vue";
import MNC from "./components/CareerLinks/mnc.vue";
import Agencies from "./components/CareerLinks/agencies.vue";
import Firms from "./components/CareerLinks/firms.vue";
import Enterprises from "./components/CareerLinks/enterprises.vue";
import Readme from "./components/FooterLinks/Readme.vue";
import ContactMe from "./components/Contact/ContactMe.vue";

Vue.use(VueRouter);

export const router = new VueRouter({
  mode: "history",
  routes: [
    { path: "/", component: Home },
    { path: "/aboutme", component: AboutMe },
    { path: "/community", component: Community },
    { path: "/projects", component: Projects },
    { path: "/mnc", component: MNC },
    { path: "/agencies", component: Agencies },
    { path: "/firms", component: Firms },
    { path: "/enterprises", component: Enterprises },
    { path: "/readme", component: Readme },
    { path: "/contact", component: ContactMe },
  ],
});
